import React, { useContext, Suspense } from "react";
import { ThemeProvider } from "@material-ui/core/styles";
import { withRouter } from "react-router-dom";
import Loader from "./views/Loader";
import { CssBaseline, Typography } from "@material-ui/core";
import { useTranslation } from "react-i18next";
import themeContext from "./theme/themeContext";

const App = () => {
  const { theme, gradient_primary } = useContext(themeContext);
  const { t } = useTranslation();
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Suspense fallback={<Loader text={t("common:loadingggg")} />}>
        <div className={`${gradient_primary} fullheightVH flex__centerlize`}>
          <Typography variant="h2" className="text-light">
            SHOP FRONT
          </Typography>
          <Typography variant="h2" className="text-light">
            DOWNLOAD APP
          </Typography>
        </div>
      </Suspense>
    </ThemeProvider>
  );
};

export default withRouter(App);
